export default {
    props: ["iconType","title","desc","required","isPass","canClick"],
    computed: {
        iconSrc: function(){
            return this.iconSrcEnum[this.iconType] || "";
        }
    },
    methods:{
        jump(){
            if(this.moduleCode == "101" || this.canClick){
                this.$router.push({name:this.iconSrc})
            }else{
                
            }
        }
    },
    data(){
        return {
            fileStatic: "/static/img/formSumIcon/",
            passIconSrc: "pass",
            arrowIconSrc: "arrow",
            iconSrcEnum: {
                101: `apply`,
                102: `personInfo`,
                103: `credit`,
                104: "work",
                105: "eme",
                106: "zhima",
                107: "yys",
                108: "shop",
                109: "wallet"
            }
        }
    }
}



