import CommonTitle from "components/CommonTitle";
import CommonFormSummary from "components/CommonFormSummary";
import custHeader from  'components/custHeader'
    export default {
        data(){
            return {
                title: "洋葱金融系统",
                listData:[],
                staticList: {
                    101:{
                        title: "申请表",
                        desc: "填写您的申请额度和期数"
                    },
                    102:{
                        title: "个人信息",
                        desc: "请补充您的该项信息"
                    },
                    103:{
                    title: "身份证",
                    desc: "请补充您的该项信息"
                },104:{
