                console.log("submit event")
            },
            getCreditList: function(){
                let _this = this;
                axiosUtil.getCreditList().then( data => {
                    if(data.code == 0){
                        this.listData = data.data.creditModuleListVoList;
                    }else{
                        //toast
                    }
                    
                })           
            }
        },
        created(){
            this.getCreditList();
        },
        props: [],
        computed:{
            // myTitle: function(){

